.movie-wrapper{
    width: 70vw;
    height: 70vh;
    padding: 20px;
    top: 50%;
    left: 50%;
    margin: 0;
    transform: translate(-50%, 13%);
    background-color: white;
    box-shadow: 0 10px 10px 0 gray;
    border-radius: 3px;
    position: absolute;
    display: grid;
    grid-template-rows: 4fr 1fr;
}

.movie-s-img{
    display: none;
}

.movie-l-img{
    padding: 0;
    margin: 0 auto;
    height: calc(50vh - 5px);
    display: table-cell;
}

.movie-data{
    padding: 0;
    margin: 0;
    display: table-cell;
}

.movie-data h3{
    padding: 0;
    margin: 0;
}

.movie-genres, .movie-torr{
    font-weight: lighter;
    color: lightslategray;
    padding: 0;
    margin: 0;
    margin-top: 0.5rem;
}

.movie-genres{
    font-size: 0.8rem;    

}
.movie-torr{
    font-size: 0.9rem;
    color: green;
}
.movie-genres li, .movie-torr li{
    display: inline-block;
    padding-right: 5px;
}

.movie-torr:first-child{
    padding-right: 1.5rem;
}

.movie-torr li a{
    background-color: green;
    color: white;
    border-radius: 0.1rem;
}
.movie-torr li a:hover,
.movie-forr li a:active, .movie-torr li a:link{
    text-decoration: none;
}

.movie-sum{
    font-size: 0.8rem;
    font-stretch: condensed;
    height: calc(20vh - 6.34em);
    overflow-y: scroll;
    -ms-overflow-style: none;
}

.movie-sum::-webkit-scrollbar{
    display: none;
}
