body{
    background-color: antiquewhite;
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
}

.movies-container{
    width: inherit;
    height: inherit;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    position: relative;
}

.loader{
    width: 100vw;
    height: 100vh;
    background-color: cornflowerblue;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    z-index: 100;
    font-stretch: expanded;
    letter-spacing: 1rem;
    color: white;
    text-align: center;
}

.loader h1{
    margin: auto;
}

.btn-container{
    width: 100vw;
    height: 100vh;
    background-color: transparent;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;

    font-stretch: extra-expanded;
    text-align: center;
}

.btn-prev, .btn-next{
    position: fixed;
    top: 45%;
    width: 10vw;
    height: 10vw;
    z-index: 50;
    display: block;
    border: none;
    color: darkgray;
    background-color: transparent;
    font-weight: bold;
    font-size: 10vw;
    font-stretch: extra-condensed;
}

.btn-prev{
    left: 0;
    border-radius: 0 50% 50% 0;
}

.btn-next{
    right: 0;
    border-radius: 50% 0 0 50%;
}